<template>
    <v-combobox
        v-model="collection"
        :items="collections"
        label="选择数据库"
        ></v-combobox>
</template>

<script>
// 2020/5/25 by oldkingnearby@gmail.com
// 数据集合选择 显示字段选择
import { get } from 'vuex-pathify'
export default {
    data:()=>({
        collection:"",
        // 获取用户有权限的数据库
    }),
    computed:{
        // 获取用户有权限的数据库
        collections:get('user/collections'),

    },

    watch:{
        // 更新数据库
        collection(){
            if (this.collection){
                this.$store.dispatch("db/update_collection",this.collection.value)
            }
            
        }
    }
}
</script>